<template>
	<div class="pdct-l-wp layout">
		<TheCompiler :tabs='tabs' @changeTab='handleChangeTab'></TheCompiler>
		<TheContentList></TheContentList>
	</div>
</template>

<script>
	import TheCompiler from '../components/product/list/TheCompiler.vue'
	import TheContentList from '../components/product/list/TheContentList' 
	export default {
		data() {
			return {
				tabs: [
					{
						tabTxt: '意外伤害',
						active: true,
						type: 1
					},
					{
						tabTxt: '医疗保险',
						active: false,
						type: 2
					},
					{
						tabTxt: '疾病保险',
						active: false,
						type: 3
					},
					{
						tabTxt: '护理保险',
						active: false,
						type: 4
					}
				]
			};
		},
		methods: {
			handleChangeTab(item){
				this.tabs.find((tab,index)=>tab!=item&&tab.active).active = false
				item.active = true
				this.$router.push('/37duc/product/list/'+item.type)
			}
		},
		computed: {
			
		},
		components: {
			TheCompiler,
			TheContentList
		}
		
	}
</script>

<style lang="scss" scoped="scoped">
.pdct-l-wp{
	wdith: 1100px;
	margin: 12px auto;
}
</style>
